/* line animation css - start */
.creasoft-wrap {
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.line_wrap {
  top: 0px;
  left: 50%;
  bottom: 0px;
  width: 80%;
  z-index: -1;
  display: block;
  position: absolute;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  border-left: 1px solid rgba(23, 22, 26, 0.06);
}

@media (max-width: 767px) {
  .line_wrap {
    border-left: 1px solid rgba(0, 0, 0, 0.03);
  }
}

.line_wrap.layout2 {
  border-left: 1px solid rgba(246, 246, 246, 0.02);
}

.line_wrap.layout2 .line_item {
  border-right: 1px solid rgba(246, 246, 246, 0.02);
}

.line_wrap:before {
  width: 1px;
  left: -1px;
  content: "";
  height: 100px;
  position: absolute;
  -webkit-animation: scroll1 15s ease-out infinite;
  animation: scroll1 15s ease-out infinite;
  background-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    from(#000000),
    to(transparent)
  );
  background-image: linear-gradient(0deg, #000000, transparent);
}

.line_item {
  width: 20%;
  float: left;
  height: 100%;
  position: relative;
  display: inline-block;
  border-right: 1px solid rgba(23, 22, 26, 0.06);
}

@media (max-width: 767px) {
  .line_item {
    border-right: 1px solid rgba(0, 0, 0, 0.03);
  }
}

.line_item:before {
  width: 1px;
  right: -1px;
  content: "";
  height: 100px;
  position: absolute;
}

.line_item:nth-child(odd):before {
  -webkit-animation: scroll2 15s ease-out infinite;
  animation: scroll2 15s ease-out infinite;
  background-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    from(transparent),
    to(#000000)
  );
  background-image: linear-gradient(0deg, transparent, #000000);
}

.line_item:nth-child(even):before {
  -webkit-animation: scroll1 15s ease-out infinite;
  animation: scroll1 15s ease-out infinite;
  background-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    from(#000000),
    to(transparent)
  );
  background-image: linear-gradient(0deg, #000000, transparent);
}

@-webkit-keyframes scroll1 {
  0% {
    top: 0px;
    opacity: 1;
  }

  50% {
    top: 50%;
  }

  100% {
    top: 100%;
    opacity: 0.5;
  }
}

@keyframes scroll1 {
  0% {
    top: 0px;
    opacity: 1;
  }

  50% {
    top: 50%;
  }

  100% {
    top: 100%;
    opacity: 0.5;
  }
}

@-webkit-keyframes scroll2 {
  0% {
    opacity: 1;
    bottom: 0px;
  }

  50% {
    bottom: 50%;
  }

  100% {
    bottom: 100%;
    opacity: 0.5;
  }
}

@keyframes scroll2 {
  0% {
    opacity: 1;
    bottom: 0px;
  }

  50% {
    bottom: 50%;
  }

  100% {
    bottom: 100%;
    opacity: 0.5;
  }
}

